export default {
	data () {
		return {
			providerList: []
		}
	},
	onLoad() {
		uni.getProvider({
			service: 'share',
			success: (e) => {
				console.log('success', e);
				let data = []
				for (let i = 0; i < e.provider.length; i++) {
					switch (e.provider[i]) {
						case 'weixin':
							data.push({
								name: '分享到微信好友',
								id: 'weixin',
								sort:0
							})
							data.push({
								name: '分享到微信朋友圈',
								id: 'weixin',
								type:'WXSenceTimeline',
								sort:1
							})
							break;
						case 'sinaweibo':
							data.push({
								name: '分享到新浪微博',
								id: 'sinaweibo',
								sort:2
							})
							break;
						case 'qq':
							data.push({
								name: '分享到QQ',
								id: 'qq',
								sort:3
							})
							break;
						default:
							break;
					}
				}
				this.providerList = data.sort((x,y) => {
					return x.sort - y.sort
				});
				
				console.log({msg: this.providerList}, 'providerList');
			},
			fail: (e) => {
				console.log('获取分享通道失败', e);
				uni.showModal({
					content:'获取分享通道失败',
					showCancel:false
				})
			}
		});
	},
